import React from "react";
import Images from "../../common/images";

function Director() {
  return (
    <>
      <div className="director-container">
        <div className="director-container-grid">
          <div className="director-img-container">
            <img className="director-img" src={Images.director} alt="" />
          </div>
          <div className="director-content-conatiner">
            <div className="director-content">
              Es licenciado en derecho de la Pontificia Universidad Catlica
              Madre y Maestra (PUCMM) y doctorando en ciencias jurdicas de la
              Universidad de Externado (Colombia). Realiz especialidades en
              derecho administrativo en la Universidad Paris II, Francia; y en
              telecomunicaciones en el Instituto de Administracin de las
              Telecomunicaciones de Canad en Montreal. Actualmente es miembro
              del Colegio Dominicano de Abogados y presidente-fundador de la
              Asociacin de Estudiantes Dominicanos en Francia y de la
              Asociacin de Juristas Dominico  Franceses; funge como secretario
              de la Cmara Dominico- Israel de Comercio. Asimismo, es docente
              de derecho administrativo y derecho de las telecomunicaciones en
              la Pontificia Universidad Catlica Madre y Maestra, en las reas
              de grado y postgrado y ocupa el cargo de director legal del
              Partido Revolucionario Moderno (PRM).
            </div>
          </div>
          <div className="director-img-container">
            <p className="director-name">SIGMUND FREUND</p>
          </div>
        </div>
      </div>
    </>
  );
}

export default Director;
